<template>
  <div class="main" ref="innerDiv">
    <div class="head" ref="banner">
      <div class="logo">徠火鍋</div>
      <div class="flist">
        <span
          v-for="(item, index) in titles"
          :key="index"
          @click="gotomenu(index, item.refdata, $event)"
          >{{ item.name }}</span
        >
      </div>
    </div>
    <div >
      <!-- <section class="banner"></section> -->
      <section class="section" ref="news">news</section>
      <section class="section" ref="features">features</section>
      <section class="section" ref="menu">menu</section>
      <section class="section" ref="about">about</section>
      <section class="section" ref="order">order</section>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      titles: [
        { name: "最新消息", refdata: "news" },
        { name: "特色", refdata: "features" },
        { name: "菜單", refdata: "menu" },
        { name: "關於我們", refdata: "about" },
        { name: "線上訂購", refdata: "order" },
      ],
    };
  },
  methods: {
    gotomenu(index, refdata, e) {
      let banner = this.$refs.banner.clientHeight;
      console.dir(this.$refs.banner)
      console.dir(banner)
      this.titles.forEach((item) => {
        if (item.refdata == refdata) {
          let top = this.$refs[refdata].offsetTop;
          this.$refs.innerDiv.scrollTop = top;
          console.dir(this.$refs['innerDiv']);
          console.log(top - banner)
        }
      });
    },
  },
  mounted() {
    // this.$refs.innerDiv.scrollTop = 1500
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.head {
  color: white;

  background-image: url("../assets/hotpot.jpg");
  background-repeat: no-repeat;
  background-size: 100vw;
  height: 100vh;
  opacity: 0.8;
}

.head > div {
  display: inline-block;
}

.logo {
  float: left;
}

.flist {
  float: right;
}

.flist > span {
  margin: 0px 15px;
}

.banner {
  background-image: url("../assets/hotpot.jpg");
  background-repeat: no-repeat;
  background-size: 100vw;
  height: 100vh;
  opacity: 0.8;
}

.section {
  height: 50vh;
}

.main {
  height: 100vh;
  overflow: scroll;
  scroll-behavior: smooth;
}
</style>



