<template>
  <div id="app">
    <title-component/>
    <!-- <inner-component/> -->
  </div>
</template>

<script>
import titleComponent from './components/title'
import innerComponent from './components/inner'
export default {
  name: 'App',
  components: {
    'title-component': titleComponent,
    'inner-component': innerComponent
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

body {
  margin: 0px;
}

body, html {
  overflow: hidden;
}
</style>



